html,
body {
    height: 100%
}

body {
    font-family: Helvetica, Arial, sans-serif;
    margin: 0;
    padding: 0;
}

body,
textarea {
    font-family:  Roboto, Helvetica, sans-serif;;
}

li {
    list-style: none;
}

li, ul, h1, h2, h3, h4, h5 {
    padding: 0;
    margin: 0;
}

input[type=text],
textarea {
    border: 1px solid #ececec;
    border-radius: 2px;
}

button {
    align-items: center;
    background-color: rgba(0, 0, 0, 0);
    border: 1.2px solid rgb(102, 102, 102);
    border-radius: 4px;
    font-weight: 600;
    padding: 4px 4px;
    margin-right: 8px;
    letter-spacing: 1.05px;
    color: rgb(102, 102, 102);
    text-transform: uppercase;
}

button:hover {
    cursor: pointer;
}